@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
*{margin:0}body{margin:0;font-family:'Roboto', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow:hidden}

.App{flex:1px 1}

.appBody{display:flex;height:100vh}

.sidebar{flex:0.05 1}.sidebar .sidebar__MenuContainer{background:white;display:flex;flex-direction:column;align-items:center;padding:10px 20px}

.sideBarMenu{display:flex;flex-direction:column;align-items:center;padding:1rem 5px;margin-bottom:10px}.sideBarMenu .MuiSvgIcon-root{color:gray;margin-bottom:0.7rem}.sideBarMenu h2{font-size:10px;color:gray}.sideBarMenu:hover{background-color:lightgray;cursor:pointer}.sideBarMenu:hover .MuiSvgIcon-root{color:red}.sideBarMenu:hover h2{color:black;font-weight:900}.sideBarMenu.selected{background-color:lightgray}.sideBarMenu.selected .MuiSvgIcon-root{color:red}.sideBarMenu.selected h2{color:red;font-weight:500}

.recommendVideo{flex:0.95 1;background:#f9f9f9;padding:1rem;overflow-y:scroll}.recommendVideo .recommendVideo__Lists{display:flex;flex-wrap:wrap;padding:1rem 0}

.videoCard{display:flex;margin-right:2rem;margin-bottom:2rem;flex-direction:column;text-decoration:none;width:270px}.videoCard .videoCard__thumbnail{width:270px;height:160px;object-fit:contain}.videoCard .videoCard__info{display:flex;padding:0.1rem}.videoCard .videoCard__info .videoCard__text{padding:0.2rem}.videoCard .videoCard__info .videoCard__text h3{font-size:1rem}.videoCard .videoCard__info .videoCard__text p{color:gray}

.drawer .drawer__Container{min-width:15rem;overflow-y:scroll}.drawer hr{border:none;height:1px;background:lightgray}.MuiDrawer-paper{overflow:hidden}

.drawerHeader{display:flex;align-items:center;padding:0.5rem;position:-webkit-sticky;position:sticky;top:0;z-index:999;background-color:white;border-bottom:1px solid #e3e3e3;overflow:hidden}.drawerHeader img{margin-left:1rem;height:1.2rem;object-fit:contain}

.drawerMenu{display:flex;align-items:center;padding:0.5rem}.drawerMenu .MuiSvgIcon-root{color:gray}.drawerMenu h2{margin-left:1rem;font-size:12px;color:gray}.drawerMenu.selected .MuiSvgIcon-root{color:red}.drawerMenu.selected h2{margin-left:1rem;font-size:12px;color:black;font-weight:900}.drawerMenu:hover{background-color:#f8f8f8;cursor:pointer}.drawerMenu:hover .MuiSvgIcon-root{color:red}.drawerMenu:hover h2{margin-left:1rem;font-size:12px;color:black;font-weight:900}

.header{flex:1 1;display:flex;align-items:center;justify-content:space-between;padding:10px 0px;padding-right:10px;position:-webkit-sticky;position:sticky;top:0;z-index:100;background:white;border-bottom:1px solid lightgray}.header .headerLeft{display:flex;align-items:center}.header .headerLeft .header__MenuIcon{padding:0 20px;display:flex;align-items:center}.header .headerLeft img{height:1.2rem;object-fit:contain}.header .headerSearch{display:flex;align-items:center;border:1px solid lightgray;width:40%;padding:0px 10px}.header .headerSearch input{height:25px;flex:1 1;border:none;border-right:1px solid lightgray}.header .headerSearch .MuiSvgIcon-root{color:gray}.header .headerIcons{display:flex;align-items:center}.header .headerIcons .MuiSvgIcon-root{margin-right:5px}.header .headerIcons .headerIcons__Avatar{width:1.7rem;height:1.7rem}

